import React from 'react'
import moment from 'moment'
import { Link } from 'react-router-dom'
import { removeHtmlTag } from '../../utils/helpers'

const BlogLists = ({ posts }) => {
	
	return (
		<div className="w-full sm:w-5/6 md:w-1/2 xl:w-1/3 mx-auto blog-lists">
			<ul className="">
				{ posts.filter(item => item?._fl_meta_?.status === 'publish')
					.sort((a, b) => b?._fl_meta_?.createdDate?.seconds - a?._fl_meta_?.createdDate?.seconds)
					.map((post, i) => (
	                <li className="mb-6" kye={i}>
			            <div className="flex flex-row flex-wrap">
			                <div className="w-full sm:w-2/3">
			                    <Link to={`/blog/${post.slug}`}>
			                        <h1 className="text-xl font-bold text-indigo-600 hover:text-indigo-700 blog-title">{post.title}</h1>
			                        <h2 className="font-light blog-excerpt">{ `${removeHtmlTag(post.content, 40)}...` }</h2>
			                    </Link>
			                </div>
			                <div className="w-full order-first text-left sm:order-last sm:w-1/3 sm:text-right blog-date">
			                    <Link to={`/blog/${post.slug}`} className="text-gray-600 font-light">
			                        {moment(post?._fl_meta_?.createdDate?.toDate()).format('DD. MMM. YYYY')}
			                    </Link>
			                </div>
			            </div>
			        </li>
		     	))}
            </ul>
		</div>
	)
}

export default BlogLists