import React, { useEffect, useState } from 'react'
import CommentBox from './CommentBox'
import CommentItem from './CommentItem'


const Comments = ({ postId, postTitle, comments, sendComment }) => {
	const [parentComment, setParentComment] = useState(null)

	useEffect(() => {
		if(parentComment){
			const el = document.getElementById("comment-box");
  			el.scrollIntoView();
		}
	}, [parentComment])

	return (
		<div className="border-t border-gray-200 comment-section">
			<h3 class="text-gray-600 font-medium py-5">{comments.length || 0} Komentar untuk "{postTitle}"</h3>
			{ comments && comments.map((comment, i) => {
				if (comment.parentId === '-'){
					return (
						<React.Fragment>
							<CommentItem key={i} comment={comment} onSetId={setParentComment}/>
							{ comments.filter(child => child.parentId === comment.id).length > 0 &&
								<div className={`ml-12 child-comment-wrapper child-of-${comment.id}`}>
									{ 	comments.filter(child => child.parentId === comment.id).map(childComment => (
											<CommentItem key={i} comment={childComment} showReply={false}/>
										)) 
									}
								</div>
							}
						</React.Fragment>
					)
				}
			})}
			<CommentBox postId={postId} sendAction={sendComment} parentComment={parentComment}/>
		</div>
	)
}

export default Comments