import React from 'react'
import moment from 'moment'

const CommentItem = ({ comment, showReply = true, onSetId }) => {
	return (
		<div className={`flex items-center mx-auto sm:flex-row flex-col mb-5 comment-${comment.id}`}>
	      <div className="flex-grow sm:text-left text-center mt-6 sm:mt-0">
	        <h2 className="text-gray-900 text-lg title-font font-bold">{comment.name} <span className="text-gray-600 font-light ml-2" style={{fontSize: '12px'}}>{comment?.date && moment(comment.date).format('DD. MMM. YYYY')}</span></h2>
	        <p className="leading-relaxed text-base">{comment.comment}</p>
	        { showReply 
	        	&& <button onClick={() => onSetId(comment)} className="text-indigo-500 inline-flex items-center">Balas
			        <svg fill="none" stroke="currentColor" stroke-linecap="round" stroke-linejoin="round" stroke-width="2" className="w-4 h-4 ml-2" viewBox="0 0 24 24">
			            <path d="M5 12h14M12 5l7 7-7 7"></path>
			        </svg>
			    </button>
	        }
	      </div>
	    </div>
	)
}

export default CommentItem