import React, { useState, useEffect } from 'react'
import { connect } from 'react-redux'
import { fetchPostAction } from '../redux/thunks'
import Layout from '../components/Layout'
import BlogLists from '../components/BlogLists'
import Spinner from '../components/Spinner'

const Home = ({ blogPosts, onFetchPosts }) => {
	const [posts, setPosts] = useState([])

	useEffect(() => {
		blogPosts.length === 0 && onFetchPosts()
	}, [])

	useEffect(() => {
		setPosts(blogPosts)
	}, [blogPosts])

	return (
		<Layout title={'Beranda'}>
      		{ blogPosts.length ? <BlogLists posts={posts}/> : <Spinner /> }
		</Layout>
	)
}

const mapStateToProps = state => ({
	blogPosts: state.posts
})

const mapDispatchToProps = dispatch => ({
	onFetchPosts: () => dispatch(fetchPostAction())
})

export default connect(mapStateToProps, mapDispatchToProps)(Home)