import {
	FETCH_POSTS_REQUEST,
	FETCH_POSTS_SUCCESS,
	FETCH_POSTS_ERROR,
	FETCH_POST_DETAIL_REQUEST,
	FETCH_POST_DETAIL_SUCCESS,
	FETCH_POST_DETAIL_ERROR,
	FETCH_COMMENT_REQUEST,
	FETCH_COMMENT_SUCCESS,
	FETCH_COMMENT_ERROR,
	SEND_COMMENT_REQUEST,
	SEND_COMMENT_SUCCESS,
	SEND_COMMENT_ERROR,
} from './actions'

const initState = {
	loading: false,
    error: false,
    success: false,
    message: null,
    posts: [],
    postDetail: {},
    comments: []
}

const blogReducer = (state = initState, action) => {
	const { type, payload } = action

	switch(type){
		case FETCH_POSTS_REQUEST:
		case FETCH_POST_DETAIL_REQUEST: {
            return {
            	...state,
            	loading: true,
            	error: false,
            	success: false,
            	message: null,
            	postDetail: {},
            	comments: []
            }
		}

		case FETCH_COMMENT_REQUEST:
		case SEND_COMMENT_REQUEST: {
			return {
            	...state,
            	loading: true,
            	error: false,
            	success: false,
            	message: null,
            }
		}

		case FETCH_POSTS_SUCCESS: {
			const { posts } = payload

            return {
            	...state,
            	loading: false,
            	success: true,
            	posts: posts
            }
		}

		case FETCH_POST_DETAIL_SUCCESS: {
			const { post } = payload

            return {
            	...state,
            	loading: false,
            	success: true,
            	postDetail: post
            }
		}

		case FETCH_COMMENT_SUCCESS: {
			const { comments } = payload

			return {
				...state,
				loading: false,
				success: true,
				comments: comments
			}
		}

		case SEND_COMMENT_SUCCESS: {
			const { message } = payload

			return {
				...state,
				loading: false,
				success: true,
				message: message,
			}
		}

		case FETCH_POSTS_ERROR:
		case FETCH_POST_DETAIL_ERROR: {
			const { message } = payload

			return {
				...state,
				loading: false,
				error: true,
				message: message,
				postDetail: {}
			}
		}

		case FETCH_COMMENT_ERROR: 
		case SEND_COMMENT_ERROR: {
			const { message } = payload

			return {
				...state,
				loading: false,
				error: true,
				message: message,
			}
		} 

		default:
            return state
	}
}

export default blogReducer