import React, { useEffect, useState } from 'react'
import { connect } from 'react-redux'
import { DiscussionEmbed } from 'disqus-react'
import moment from 'moment'
import { request } from '../utils/request'
import Layout from '../components/Layout'
import { imgParser } from '../utils/helpers'
import { fetchPostDetailAction, fetchCommentAction, sendCommentAction } from '../redux/thunks'
import Spinner from '../components/Spinner'
import NotFoundLogo from '../assets/images/error-404.svg'
import Article from '../assets/images/article.svg'
import Comments from '../components/Comments'

const Detail = ({ match, onFetchDetail, onFetchComments, onSendComment,postDetail, request, comments }) => {
	const { slug } = match.params
	const [detail, setDetail] = useState([])

	useEffect(() => {
		onFetchDetail(slug)
	}, [slug])

	useEffect(() => {
		setDetail(postDetail)
		onFetchComments(postDetail?.id)
	}, [postDetail])

	return (
		<Layout title={request.error ? 'Artikel tidak ditemukan' : detail?.title}>
			{ request.success && Object.keys(detail).length > 0 && 
				<article className="max-w-xl mx-auto blog-detail">
					<header class="text-center mb-6 sm:mb-12">
			            <h1 class="text-2xl sm:text-4xl font-bold text-indigo-600 mb-5" style={{fontFamily: 'Montserrat'}}>{detail.title}</h1>
			            <h3 class="text-gray-600 font-light blog-date">
			                Published at {moment(detail?._fl_meta_?.createdDate?.toDate()).format('DD. MMM. YYYY')}
			            </h3>
			        </header>
				    <section className="prose my-4 content" dangerouslySetInnerHTML={{__html: imgParser(detail.content)}}></section>
				    { detail?.enableComments 
				    	? <Comments postId={detail?.id} comments={comments} postTitle={detail?.title} sendComment={onSendComment}/>
				    	: <p className="my-2 bg-gray-100 py-2 px-5 rounded">Komentar dinonaktifkan oleh Admin</p>
				    }
				</article>
			}

			{ request.loading && 
				<section className="flex items-center justify-center flex-col w-full py-10">
					<Spinner />
		     	</section>
			}

			{ request.error && 
				<section className="flex items-center justify-center flex-col w-full py-20 blog-detail">
					<img className="lg:w-1/6 md:w-2/6 w-4/6 mb-10 object-cover object-center rounded" alt="not found" src={NotFoundLogo}/>
		     		<h2 className="title-font sm:text-3xl text-2xl mb-10 font-medium text-gray-900">Artikel tidak ditemukan!</h2>
		     	</section>
			}
		</Layout>
	)
}

const mapStateToProps = state => ({
	postDetail: state.postDetail,
	request: {
		success: state.success,
		loading: state.loading,
		error: state.error
	},
	comments: state.comments
})

const mapDispatchToProps = dispatch => ({
	onFetchDetail: slug => dispatch(fetchPostDetailAction(slug)),
	onFetchComments: postId => dispatch(fetchCommentAction(postId)),
	onSendComment: (data, callback) => dispatch(sendCommentAction(data, callback))
})

export default connect(mapStateToProps, mapDispatchToProps)(Detail)