const removeHtmlTag = (html, length = 250) => {
	const result = html.replace(/<[^>]+>/g, '');
	return result.substring(0, length);
}

const mapMonth = (index) => {
	 const months = { 
	 	1: 'Jan',2: 'Feb',3: 'Mar',4: 'Apr',5: 'Mei',6: 'Jun',
	 	7: 'Jul',8: 'Agu',9: 'Sep',10: 'Okt',11: 'Nov',12: 'Des'
	  };
	  return months[index];
}

const getDate = (date) => {
	return new Date(date * 1000).toLocaleDateString('id-ID').split('/')
}

const stickySidebar = (selector) => {
	const sticky = document.querySelector(selector);
	if (sticky) {
		if(window.matchMedia("(min-width: 1024px)").matches){
	  		window.Sticky.init(sticky);
		}
	}
}

const trimTitle = (title) => {
	let newTitle

	if(title.toString().length > 30){
		newTitle = title.substring(0, 40) + '...'
	} else {
		newTitle = title
	}

	return newTitle
}

const imgParser = (str) => {
	return str 
		? str.replace(/{{(.*?)}}/g, `
			<div class="flex items-center justify-center flex-col">
				<$1 loading="lazy" class="object-cover object-center h-full w-full border border-gray-200 rounded"/>
			</div>`) 
		: null
}

export { removeHtmlTag, mapMonth, getDate, stickySidebar, trimTitle, imgParser }