class API {
	constructor(){
		if (!firebase.apps.length) {
			firebase.initializeApp(firebaseConfig)
		}
		
		this.db = firebase.firestore()
	}

	auth = this.auth

	blogs = () => 
		this.db.collection('fl_content')

	blog = id => 
		this.db.doc(`fl_content/${id}`)

	comments = () =>
		this.db.collection('comments')
}

export default (new API())