import React, { useState, useEffect } from 'react'

const CommentBox = ({ postId, sendAction, parentComment, replyMode = false }) => {
	const [showAlert, setShowAlert] = useState(false)
	const [showSuccess, setShowSuccess] = useState(false)
	const [userComment, setUserComment] = useState(JSON.parse(localStorage.getItem('userComment')))
	const [commentData, setCommentData] = useState({
		name: '',
		email: '',
		comment: '',
		postId: postId,
		parentId: '-',
		date: Date.now()
	})

	useEffect(() => {
		if(userComment){
			setCommentData(prevState => ({
				...prevState,
				name: userComment?.name, 
				email: userComment?.email
			}))
		}
	}, [userComment])


	useEffect(() => {
		setShowAlert(true)
		setCommentData(prevState => ({
			...prevState,
			parentId: parentComment?.id || '-'
		}))
	}, [parentComment])

	useEffect(() => {
		setTimeout(() => setShowSuccess(false), 3000) 
	}, [showSuccess])


	const handleChange = e => {
		const { name, value } = e.target
		setCommentData(prevState => ({
			...prevState,
			[name]: value
		}))
	}

	const handleCancelReply = () => {
		setShowAlert(false)
		setCommentData(prevState => ({
			...prevState,
			parentId: '-'
		}))
	}

	const submitComment = e => {
		e.preventDefault()
		sendAction(commentData, () => {
			localStorage.setItem('userComment', JSON.stringify({
				name: commentData.name,
				email: commentData.email
			}))
			setCommentData(prevState => ({
				...prevState,
				name: '',
				email: '',
				comment: '',
				postId: postId,
				parentId: '-'
			}))
			setShowAlert(false)
			setShowSuccess(true)
		})
	}

	return (
		<section className="text-gray-600 body-font border-t border-gray-200" id="comment-box">
		  <form onSubmit={submitComment} className="container pt-10 pb-24 mx-auto">
		    <div className="w-full mx-auto items-end">
		    	<div className="relative w-full">
			        <label for="comment" className="leading-7 text-sm text-gray-600">Komentar</label>
			        { parentComment && showAlert && <p className="mb-2 bg-gray-100 py-2 px-5 rounded">Anda membalas komentar <strong>{parentComment?.name} </strong><span onClick={handleCancelReply}className="font-bold cursor-pointer" style={{float: 'right', color: 'red'}}>&times; Batal</span></p>}
			        <textarea value={commentData.comment} required onChange={handleChange} id="comment" name="comment" className="w-full bg-gray-100 bg-opacity-50 rounded border border-gray-300 focus:border-indigo-500 focus:bg-transparent focus:ring-2 focus:ring-indigo-200 text-base outline-none text-gray-700 py-1 px-3 leading-8 transition-colors duration-200 ease-in-out"></textarea>
		      	</div>
		      	{ userComment && <p className="my-2 bg-gray-100 py-2 px-5 rounded">Komentar sebagai <strong>{ userComment.name }</strong></p>}
		     	{ !userComment?.name && !userComment?.email &&
			     	<>
			     		<div className="relative w-full">
		     		        <label for="name" className="leading-7 text-sm text-gray-600">Nama</label>
		     		        <input value={commentData.name} required onChange={handleChange} type="text" id="name" name="name" className="w-full bg-gray-100 bg-opacity-50 rounded border border-gray-300 focus:border-indigo-500 focus:bg-transparent focus:ring-2 focus:ring-indigo-200 text-base outline-none text-gray-700 py-1 px-3 leading-8 transition-colors duration-200 ease-in-out" />
		     		      </div>
		     		      <div className="relative w-full">
		     		        <label for="email" className="leading-7 text-sm text-gray-600">Email</label>
		     		        <input value={commentData.email} required onChange={handleChange} type="email" id="email" name="email" className="w-full bg-gray-100 bg-opacity-50 rounded border border-gray-300 focus:border-indigo-500 focus:bg-transparent focus:ring-2 focus:ring-indigo-200 text-base outline-none text-gray-700 py-1 px-3 leading-8 transition-colors duration-200 ease-in-out" />
		     		    </div>
			     	</>
		     	}
		      <button type="submit" className="mt-3 text-white bg-indigo-500 border-0 py-2 px-8 focus:outline-none hover:bg-indigo-600 rounded text-lg">Kirim</button>
		    </div>
		    { showSuccess && <p className="my-2 bg-green-100 py-2 px-5 rounded">Komentar berhasil di kirim</p>}
		  </form>
		</section>
	)
}

export default CommentBox